.min-graph-info{cursor:pointer;max-width:300px;width:100%;margin-right:8px;padding:8px 16px 0;position:relative}.min-graph-info:after{content:"";position:absolute;width:1px;left:0;top:15%;height:70%;background:rgba(186,186,186,.5)}.min-graph-info.selected:after{content:"";position:absolute;width:1px;left:0;top:0;height:100%;background:#4b45c6}.min-graph-info.selected .min-graph-info-value{font-size:52px;font-weight:normal;font-weight:initial}.min-graph-info-header{display:flex;justify-content:space-between}.min-graph-info-header-title{letter-spacing:.8px;line-height:24px;font-size:13px;font-size:16px;white-space:nowrap;text-transform:uppercase;color:#a3a3a3}.min-graph-info-header-title svg{width:18px;height:18px;margin-left:4px}.min-graph-info-header-icon{color:#4b45c6}.min-graph-info-value{font-size:28px;font-weight:300}